body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

::-webkit-scrollbar-button {
  background-image: url('');
  background-repeat: no-repeat;
  width: 5px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: #111111;
}

::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #333333;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #56999f;
}

::-webkit-scrollbar {
  width: 4px;
}

.Main {
  position: relative;

  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  color: white;
  font-size: calc(10px + 2vmin);

  background-color: #111111;

  background-image:
    -webkit-radial-gradient(#111111 10%, #333333 2%, transparent 13%, #111111 10%, #333333 2%, transparent 13%),
    -webkit-radial-gradient(#111111 10%, #333333 2%, transparent 13%, #111111 10%, #333333 2%, transparent 13%);

  background-image:
    radial-gradient(#111111 10%, #333333 2%, transparent 13%, #111111 10%, #333333 2%, transparent 13%),
    radial-gradient(#111111 10%, #333333 2%, transparent 13%, #111111 10%, #333333 2%, transparent 13%);

  background-size: 60px 60px;

  -webkit-animation: bgAnimate 5s linear infinite;

          animation: bgAnimate 5s linear infinite;
}

@-webkit-keyframes bgAnimate {
  from {
    background-position: 30px 30px, 0 0;
  } to {
    background-position: 60px 60px, 30px 30px;
  }
}

@keyframes bgAnimate {
  from {
    background-position: 30px 30px, 0 0;
  } to {
    background-position: 60px 60px, 30px 30px;
  }
}
.Header {
    background: rgba(85, 85, 85, .15);

    width: 100%;
}

.Header-Logo {
    width: 100px;
    height: 100px;

    border-radius: 100%;

    margin: 5px 20px;

    -webkit-transition: .4s all ease-in-out;

    transition: .4s all ease-in-out;
}

.Nav {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;

    padding: 10px;
}

.Nav-Item {
    color: #fff;
    text-decoration: none;

    display: flex;
    align-items: center;

    margin: 0 20px;
}

.Header-Icon {
    width: 50px;
    height: 50px;

    -webkit-transition: .2s all ease-in-out;

    transition: .2s all ease-in-out;
}

.Header-Icon:hover {
    width: 70px;
    height: 70px;
}

.Header-Span_iconed{
    margin-left: 20px;
}

@media (max-width: 538px) {
    .Header-Logo {
        margin: 5px 10px;
    }

    .Nav-Item {
        margin: 0 10px;
    }
}

@media (max-width: 450px) {
    .Header-Icon {
        width: 40px;
        height: 40px;
    }

    .Header-Icon:hover {
        width: 50px;
        height: 50px;
    }

    .Header-Logo {
        width: 80px;
        height: 80px;
    }
}

@media (max-width: 390px) {
    .Nav-Item {
        margin: 0 5px;
    }

    .Header-Logo {
        margin: 5px 5px;
    }
}
.Article {
    display: flex;

    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    background: rgba(85, 85, 85, 0.15);

    padding: 20px;
    border-radius: 5px;

    width: 100%;
}

.Article:not(:first-child) {
    margin-top: 20px;
}

.Article a {
    color: #9393f9;
}

.Article-Header {
    display: flex;

    flex-direction: column;
    justify-content: flex-start;
}

.Article-Title {
    margin: 0;
}

.Article-Date {
    margin-top: 5px;

    font-size: 14px;
    font-weight: 100;

    color: #939393;
}
.Paginator-Tabs {
    display: flex;

    justify-content: center;
    align-items: center;
}

.Paginator-Tab {
    width: 15px;
    height: 15px;

    -webkit-transition: .2s all linear;

    transition: .2s all linear;

    background: rgba(255, 255, 255, .3);

    border-radius: 100%;

    margin: 10px;
}

.Paginator-Tab_active {
    background: #ffffff;
}
.Body {
    width: 80%;

    margin: 10px auto;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    flex-grow: 1;

    font-size: calc(10px + 2vmin);
    color: white;
}

.Body-Articles {
    flex-grow: 2;

    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (max-width: 538px) {
    .Body {
        width: 90%;
    }
}
