.Body {
    width: 80%;

    margin: 10px auto;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    flex-grow: 1;

    font-size: calc(10px + 2vmin);
    color: white;
}

.Body-Articles {
    flex-grow: 2;

    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (max-width: 538px) {
    .Body {
        width: 90%;
    }
}