.Header {
    background: rgba(85, 85, 85, .15);

    width: 100%;
}

.Header-Logo {
    width: 100px;
    height: 100px;

    border-radius: 100%;

    margin: 5px 20px;

    transition: .4s all ease-in-out;
}

.Nav {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;

    padding: 10px;
}

.Nav-Item {
    color: #fff;
    text-decoration: none;

    display: flex;
    align-items: center;

    margin: 0 20px;
}

.Header-Icon {
    width: 50px;
    height: 50px;

    transition: .2s all ease-in-out;
}

.Header-Icon:hover {
    width: 70px;
    height: 70px;
}

.Header-Span_iconed{
    margin-left: 20px;
}

@media (max-width: 538px) {
    .Header-Logo {
        margin: 5px 10px;
    }

    .Nav-Item {
        margin: 0 10px;
    }
}

@media (max-width: 450px) {
    .Header-Icon {
        width: 40px;
        height: 40px;
    }

    .Header-Icon:hover {
        width: 50px;
        height: 50px;
    }

    .Header-Logo {
        width: 80px;
        height: 80px;
    }
}

@media (max-width: 390px) {
    .Nav-Item {
        margin: 0 5px;
    }

    .Header-Logo {
        margin: 5px 5px;
    }
}