.Paginator-Tabs {
    display: flex;

    justify-content: center;
    align-items: center;
}

.Paginator-Tab {
    width: 15px;
    height: 15px;

    transition: .2s all linear;

    background: rgba(255, 255, 255, .3);

    border-radius: 100%;

    margin: 10px;
}

.Paginator-Tab_active {
    background: #ffffff;
}